body {
  margin: 0;
  background-color: white;
  font-family: sans-serif;
}

aside {
  position:fixed;
  width: 250px;
  height: 100%;
  top: 0;
  left: 0;
  padding-top: 45px;
}

aside img {
  width: 75%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

aside ul {
  list-style: none;
  font-size: 1.2em;
  font-weight: bold;
  margin-top: 40px;
  margin-bottom: 40px;
}

aside li {
  margin-bottom: 15px;
}

#nav-link {
  text-transform:uppercase;
  text-decoration: none;
  color: black
}

#nav-link.active {
  text-decoration: none;
  color: #403681;
}

#nav-link:hover {
  color: #403681;
}

#social-icons {
  width: 75%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-around;
}

.fa {
  padding: 8px;
  width: 18px;
  text-align: center;
  text-decoration: none;
  border-radius: 50%;
  background-color: black;
  color: white;
}

#content {
  margin-left: 250px;
  width: 700px;
  padding-top: 45px;
}

.youtube-video {
  margin-bottom: 30px;
}

.press-shot {
  width: 100%;
  margin-bottom: 0;
}

.press-shot-caption {
  margin-top: 0;
  margin-bottom: 30px;
  text-align: right;
  font-family: monospace;
}

#picture {
  width: 100%;
}

#mc_embed_signup {
  font:14px Helvetica,Arial,sans-serif;
  background-color: lightgray;
  padding: 20px;
  width: 50%;
}

.shows-blurb {
  margin-top: 0;
}

.shows {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.show {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: monospace;
}

.shows-blurb {
  text-decoration: none;
  color: black;
}

.shows-blurb:hover {
  color: #403681;
}

.show-button {
  background-color: black;
  color: white;
  padding: 10px;
  text-decoration: none;
  margin-left: 10px;
}

.show-button:hover {
  background-color: #404040;
  color: white;
  padding: 10px;
  text-decoration: none;
  margin-left: 10px;
}

.show-location {
  margin-left: 50px;
}

.show-geo {
  margin-top: 0;
}

.show-venue {
  margin-bottom: 0;
}

.song p {
  margin: 0;
}

.song h3 {
  margin-bottom: 5px;
}

.song {
  margin-bottom: 30px;
}

.elevator-pitch {
  display: flex;
  flex-direction: row;
}

.elevator-pitch-image {
  width: 50%;
}

.elevator-pitch-text {
  margin-top: 0;
  margin-left: 30px;
  text-align: justify;
  text-justify: inter-word;
}

.bio-text {
  text-align: justify;
  text-justify: inter-word;
}

.email-address {
  color: #403681;
}

.release {
  display: flex;
  flex-direction: row;
  margin-bottom: 30px;
}

.release-art {
  width: 50%;
}

.release-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.release-title {
  margin-top: 0;
  margin-bottom: 5px;
  margin-left: 30px;

}

.release-date {
  margin-top: 0;
  margin-left: 30px;
}

.release-notes {
  font-family: monospace;
  font-size: 0.7em;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 30px;
}

.release-buttons {
  margin-top: 20px;
  margin-left: 30px;
  display: flex;
}

.release-button {
  background-color: black;
  color: white;
  padding: 10px;
  text-decoration: none;
  margin-right: 10px;

}

.release-button:hover {
  background-color: #404040;
  color: white;
  padding: 10px;
  text-decoration: none;
}

.contact-blurb {
  margin-top: 0;
}

